$border-radius: 4px;

.#{$prefix}header {
  margin: 0;
  background-color: $gray1;
  color: #fff;
  padding: 10px 20px;
  font-size: 18px;
  font-weight: 600;
  font-family: "Futura", sans-serif;
  border-radius: $border-radius $border-radius 0 0;
  text-align: center;
}

.#{$prefix}vis {
  margin: 0;
  background: #fff;
  border-radius: 0 0 $border-radius $border-radius;
  position: relative;
  border-top: none;
  box-shadow: 0 6px 10px rgba($gray2, 0.7);

  ##{$prefix}map-svg {
    width: 100%;
  }

  .state,
  .district {
    fill: none;
    stroke: #03172d;
    stroke-linejoin: round;
  }

  .district:hover,
  .state:hover {
    mask: url(#hoverMask);
    cursor: pointer;
  }

  .district:active,
  .state:active {
    cursor: move;
    cursor: grab;
  }
}

.#{$prefix}map {
  width: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  background: #fff;
  background-image: radial-gradient($gray2 1px, transparent 0),
    radial-gradient($gray2 1px, transparent 0);
  background-size: 10px 10px;
  svg:active {
    cursor: move;
    cursor: grab;
  }

  @media screen and (min-width: 600px) {
    flex-direction: row;

    ##{$prefix}map-svg {
      min-height: 450px;
    }
  }
}

$details-color: $gray1;

.#{$prefix}details {
  display: none;
  font-size: 14px;
  padding: 10px 20px;
  min-width: 225px;
  background: $details-color;
  color: #fff;
  border-radius: 0 0 $border-radius $border-radius;
  position: relative;

  h4 {
    color: #fff;
    font-size: 18px;
  }

  p {
    margin-top: 0;
  }

  a {
    color: inherit;
  }

  h4 {
    font-weight: strong;
    font-family: futura;
    margin: 0;
  }

  table {
    padding-bottom: 10px;
    width: 100%;
    border-collapse: collapse;
    margin-top: 10px;
    margin-bottom: 30px;
  }

  tr {
    border-bottom: 1px solid $gray4;
    padding: 5px 0;

    &:first-child {
      border-top: 2px solid $gray4;
    }
  }

  td {
    padding: 5px 0;
  }

  td:nth-child(2) {
    font-family: $mono;
    text-align: right;
  }

  @media screen and (min-width: 600px) {
    border-radius: 0 0 $border-radius 0;

    &::after {
      right: 100%;
      top: 50%;
      border: solid transparent;
      content: " ";
      height: 0;
      width: 0;
      position: absolute;
      pointer-events: none;
      border-right-color: $details-color;
      border-width: 20px;
      margin-top: -20px;
    }
  }
}

.#{$prefix}tooltip {
  position: absolute;
  z-index: 999;
  background: $gray1;
  color: #fff;
  padding: 6px 10px;
  font-size: 10px;
  border-radius: 20px;
  box-shadow: 0 4px 10px 0 rgba($gray1, 0.2);
}

.#{$prefix}legend {
  padding: 10px 10px 0 10px;
  background: url("/img/dfp-logo.jpg");
  background-position: 98% 50%;
  background-repeat: no-repeat;
  background-size: 20%;
  border-top: 1px solid $gray2;
  position: relative;

  span {
    font-size: 12px;
    display: block;
    margin-bottom: 5px;
  }

  svg {
    bottom: 10px;
    max-width: 250px;
    display: block;
  }

  &::after {
    content: "Click for more details";
    position: absolute;
    top: -40px;
    left: 50%;
    left: 20px;
    font-size: 14px;
    font-weight: bold;
    background: rgba(255, 255, 255, 0.6);
    padding: 5px 10px;
  }
}
